import { h } from 'preact';
import { css, setup } from 'goober';
setup(h);


export default function SimonSays({size } = {}) { 
  size = size || 400;
  const styles = css`
  @import url('https://fonts.googleapis.com/css2?family=Sarpanch:wght@800&display=swap');
  ${reset}
  --size: ${size}px;
  position: relative;
  width: var(--size);
  height: var(--size);
  background-color:black;
  border-radius: 999px;
  font-family: Sarpanch;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;


  .button-container {
    display:grid;
    place-content: center;
  }
  .outer-ring {
    width:100%;
    height:100%;
    position:absolute;
    border: ${size * 0.04}px solid black;
    border-radius: 999px;
    z-index:100;
  }

  .vertical-border {
    position:absolute;
    height:95%;
    z-index:50;
    top:50%;
    left:50%;
    width: ${size * 0.04}px;
    transform: translate(-50%, -50%);
    background-color:black;
  }

  .horizontal-border {
    position:absolute;
    width:95%;
    z-index:50;
    top:50%;
    left:50%;
    height: ${size * 0.04}px;
    transform: translate(-50%, -50%);
    background-color:black;
  }

  `
  return (
    <div class={styles}>      
      <CenterConsole size={size * .8}/>
      <div class="outer-ring"></div>
      <div class="vertical-border"></div>
      <div class="horizontal-border"></div>
      <div class="button-container"><GameButton position={1} size={size}/></div>
      <div class="button-container"><GameButton position={2} size={size}/></div>
      <div class="button-container"><GameButton position={3} size={size}/></div>
      <div class="button-container"><GameButton position={4} size={size}/></div>
    </div>
  );
}

function CenterConsole({size}) {
  const mySize = size * 0.5;
  const styles = css`
  z-index:100;
  overflow: hidden;
  position:absolute;
  background-color: black;
  border-radius: 999px;
  border:  ${size * 0.04}px solid black;
  color:white;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: ${mySize}px;
  width: ${mySize}px;
  display:flex;
  flex-direction: column;
  
  .top-section {
    height:45%;
    background-color: #111111;
    display: flex;
    justify-content: center;
    align-items: end;
    font-size: ${size * .045}px;
    padding-bottom:${size * .02}px;
  }
  .bottom-section {
    height:55%;
    background-color:#222222;
  }
  `
  return <div class={styles}>
<div class="top-section">
SIMON SAYS
</div>
<div class="bottom-section">

</div>

  </div>
}

function GameButton({position, size, className, ...props}) {
  const styles = css`
    color: green;
    fill: currentColor;
    ${position === 2 && `
      color: red;
      transform: rotateZ(90deg);
    ` || undefined}

    ${position === 3 && `
    color: yellow;
    transform: rotateZ(270deg);
  ` || undefined}

  ${position === 4 && `
  color: blue;
  transform: rotateZ(180deg);
` || undefined}
  `
  const innerSize = size * 0.5;
  return (
  <svg class={styles}  width={`${innerSize}px`} height={`${innerSize}px`} {...props} viewBox="0 0 50 50">
     <circle cx="50" cy="50" r="50"/>
   {/* <path d=" M2,2 Q8,2 8,8 M8,8 L4,8 Q4,6 2,6 L2,2"></path> */}
  </svg>
  )
}


const reset = `
/*
  1. Use a more-intuitive box-sizing model.
*/
*, *::before, *::after {
  box-sizing: border-box;
}
/*
  2. Remove default margin
*/
* {
  margin: 0;
}
/*
  3. Allow percentage-based heights in the application
*/
html, body {
  height: 100%;
}
/*
  Typographic tweaks!
  4. Add accessible line-height
  5. Improve text rendering
*/
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}
/*
  6. Improve media defaults
*/
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}
/*
  7. Remove built-in form typography styles
*/
input, button, textarea, select {
  font: inherit;
}
/*
  8. Avoid text overflows
*/
p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}
/*
  9. Create a root stacking context
*/
#root, #__next {
  isolation: isolate;
}
`